/*
* 角色权限
* dangw
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {
	Breadcrumb,
	Icon,
	Input,
	Modal,
	Tabs,
	Dropdown,
	Menu,
	Form,
	message,
	Row,
	Col,
	Radio,
	Checkbox,
	Button
} from 'antd';
import CastMembers from './CastMembers';
//import PermissionView from './PermissionView';
//import DataAuthority from './DataAuthority';
import pinyin4js from 'pinyin4js';  // 打包注意

const {TabPane} = Tabs;
const {confirm} = Modal;

@Form.create()
@inject('UserStore')
@observer
class RoleNew extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			visible: false,
			title: "新建角色",
			data: [],
			activeKey: '1',
			name: '',
			obj: {},
			checkedKeys: [],   // 功能查看
			treeData: [],
			expandedKeys: [],
			dataTreeData: [], //数据填报
			dataExpandedKeys: [],
			sales: "", // 销售
			purchasing: "", // 采购
			auth: [], //管理员
		}
	}

	componentDidMount() {
		this.gettable();
		this.props.UserStore.getAppPermissionList();
	}

	gettable = () => {
		this.props.UserStore.getRoleListByAppId().then((res) => {
			this.setState({
				data: Object.assign([], res.body.filter((item) => {
						return item.code !== "wisdomsee.group"
					}).map((v, vindex) => {
						if (vindex == 0) {
							return Object.assign(v, {selected: true})
						} else {
							return v;
						}
					})
				)
			}, () => {
				// 进入默认取第一条数据
				//console.log(this.state.data, '删除')
				const param = this.state.data[0];
				this.setState({
					name: param ? param.name : ""
				})
				if (param) {
					this.props.UserStore.rolecode = param.code;
					this.props.UserStore.roleId = param.id;
					this.props.UserStore.rolepageIndex = 1;
					this.props.UserStore.rolepageSize = 10;
					this.props.UserStore.getRoleList();
					// 获取已有的权限
					this.props.UserStore.getRolePermission().then((res) => {
						this.setState({
							checkedKeys: res.map((v) => {
								return v.id
							}),
							sales: res.map((v) => {
								return v.id
							}).filter((m) => {
								return m == "orders.sales.edit.all" || m == "orders.sales.edit.self";
							})[0] || "", // 销售
							purchasing: res.map((v) => {
								return v.id
							}).filter((m) => {
								return m == "orders.purchasing.edit.all" || m == "orders.purchasing.edit.self";
							})[0] || "", // 采购
							auth: res.map((v) => {
								return v.id
							}).filter((m) => {
								return m == "orders.auth.view.all" || m == "orders.auth.edit.all";
							}) || [], //管理员
						})
					});
				} else {
					// 没有数据时清空
					this.props.UserStore.rolecode = "";
					this.props.UserStore.roleId = " ";
					this.props.UserStore.rolepageIndex = 1;
					this.props.UserStore.rolepageSize = 10;
					this.props.UserStore.roletotalCount = 0;
					this.props.UserStore.roletableData = Object.assign([]);
				}
			})
		});
	}

	callback = (activeKey) => {
		this.setState({
			activeKey
		}, () => {
			if (activeKey == "2") {
				this.getData();
			}
			if (activeKey == "3") {
				this.getDataPermission();
			}
		});
	}

	// 获取功能查看权限
	getData = () => {
		// 获取权限
		this.props.UserStore.getAllReportClassifys({type: 0}).then((res) => {

			// 获取所有的父节点
			function getNodes(arr) {
				let array = [];
				var traverse = function serverArray(arr) {
					for (var item = 0; item < arr.length; item++) {
						if (arr[item].children) {
							array.push(arr[item].permission)
							serverArray(arr[item].children)
						}
					}
				}
				traverse(arr);
				return array
			}

			this.setState({
				treeData: res.map((item, index) => {
					return Object.assign({}, {
						title: item.name,
						key: item.permission,
						children: item.children && item.children.map((vitem, vindx) => {
							return {
								title: vitem.name,
								key: vitem.permission,
								children: vitem.children && vitem.children.map((mitem, mindx) => {
									return {
										title: mitem.name,
										key: mitem.permission,
									}
								})
							}
						})
					})
				}),
				expandedKeys: Object.assign([], getNodes(res))
			});
		});

		// 获取已有的权限
		this.props.UserStore.getRolePermission().then((res) => {
			this.setState({
				checkedKeys: res.map((v) => {
					return v.id
				}),
				sales: res.map((v) => {
					return v.id
				}).filter((m) => {
					return m == "orders.sales.edit.all" || m == "orders.sales.edit.self";
				})[0] || "", // 销售
				purchasing: res.map((v) => {
					return v.id
				}).filter((m) => {
					return m == "orders.purchasing.edit.all" || m == "orders.purchasing.edit.self";
				})[0] || "", // 采购
				auth: res.map((v) => {
					return v.id
				}).filter((m) => {
					return m == "orders.auth.view.all" || m == "orders.auth.edit.all";
				}) || [], //管理员
			})
		});
	}

	// 获取数据填报权限
	getDataPermission = () => {
		this.props.UserStore.getAllReportClassifys({type: 1}).then((res) => {

			// 获取所有的父节点
			function getNodes(arr) {
				let array = [];
				var traverse = function serverArray(arr) {
					for (var item = 0; item < arr.length; item++) {
						if (arr[item].children) {
							array.push(arr[item].permission)
							serverArray(arr[item].children)
						}
					}
				}
				traverse(arr);
				return array
			}

			this.setState({
				dataTreeData: res.map((item, index) => {
					return Object.assign({}, {
						title: item.name,
						key: item.permission,
						children: item.children && item.children.map((vitem, vindx) => {
							return {
								title: vitem.name,
								key: vitem.permission,
								children: vitem.children && vitem.children.map((mitem, mindx) => {
									return {
										title: mitem.name,
										key: mitem.permission,
									}
								})
							}
						})
					})
				}),
				dataExpandedKeys: Object.assign([], getNodes(res))
			}, () => {

			})
		});
	}

	handleMenuClick = (e) => {
		if (e.key == '1') {
			this.setState({
				visible: true,
				title: "新建角色",
				name: "",
			})
		}
	}

	handleMenuClick1 = (key, item) => {
		let _this = this;
		if (key == '1') {
			_this.setState({
				visible: true,
				title: "编辑角色",
				name: item.name,
				obj: Object.assign({}, item)
			})
		} else if (key == '2') {
			confirm({
				title: '您确定要删除吗?',
				okText: '确定',
				okType: 'danger',
				cancelText: '取消',
				onOk() {
					const param = {
						roleId: item.id
					}
					_this.props.UserStore.deleteRole(param).then((res) => {
						if (res) {
							message.success('删除成功')
							_this.gettable();
						} else {
							message.error('删除失败');
						}
					});
				},
				onCancel() {
					console.log('Cancel');
				},
			});
		}
	}

	handleOk = e => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			// 角色名称首字母大写
			const code = pinyin4js.convertToPinyinString(values['name'], '', pinyin4js.FIRST_LETTER)
			//console.log(code)
			if (!err) {
				// 新建角色
				if (this.state.title == "新建角色") {
					const param = {
						"role": Object.assign({name: values['name']}, {
							id: null,
							applicationIds: "orders",
							code: "orders.role." + code,
						})
					};
					this.props.UserStore.addRole(param).then((res) => {
						if (res == true) {
							message.success("保存成功")
							this.handleCancel();
							// 重查
							this.gettable();
						} else {
							message.error("保存失败")
						}
					});
				} else {
					// 编辑角色
					const param = {
						"role": Object.assign({name: values['name']}, {
							id: this.state.obj.id,
							applicationIds: "wisdomsee",
							code: "wisdomsee.role." + code,
						})
					};
					this.props.UserStore.updateRole(param).then((res) => {
						if (res == true) {
							message.success("保存成功")
							this.handleCancel();
							// 重查
							this.gettable();
							this.setState({
								visible: false,
								name: values['name']
							}, () => {
								// 重查
								this.gettable();
							});
						} else {
							message.error("保存失败")
						}
					});
				}
			}
		});
	};

	handleCancel = e => {
		this.props.form.resetFields();
		this.setState({
			visible: false,
		});
	};

	// 切换角色
	handleClick = (param) => {
		let dataArray = JSON.parse(JSON.stringify(this.state.data));
		dataArray.map((res) => {
			return Object.assign(res, {selected: false})
		})
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
		this.setState({
			data: Object.assign([], dataArray),
			name: param.name
		})

		this.props.UserStore.rolecode = param.code;
		this.props.UserStore.roleId = param.id;

		// 1、重查用户列表
		this.props.UserStore.rolepageIndex = 1;
		this.props.UserStore.rolepageSize = 10;
		this.props.UserStore.getRoleList();

		// 2、重查功能权限
		this.getData();

		// 3、重查数据权限
		this.getDataPermission();
		this.props.UserStore.checkedKeys = Object.assign([]);
		this.props.UserStore.checkedList = Object.assign([]);

	}

	updateState = (param) => {
		this.setState({
			checkedKeys: Object.assign([], param)
		});
	}

	// 权限设置
	onChangeRadio = (type, e) => {
		this.setState({
			[type]: e.target.value
		});
	}

	onChangeCheck = (checkedValues) => {
		this.setState({
			auth: checkedValues
		});
	}

	handleSub = () => {
		const data = [this.state.sales, this.state.purchasing, ...this.state.auth];
		const permissionIds = data.filter(item=>item!=="");
		const params = {
			permissionIds: permissionIds,
			roleId: this.props.UserStore.roleId
		}
		this.props.UserStore.addRolePermissions(params).then((res) => {
			if (res) {
				message.success("操作成功")
			}
		});
	}

	render() {
		const {data, activeKey, name} = this.state;
		const menu = (
			<Menu onClick={this.handleMenuClick}>
				<Menu.Item key="1">新建角色</Menu.Item>
			</Menu>
		);
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 20},
			},
		};


		return (
			<div>
				<div className="role_con" style={{minHeight: '500px'}}>
					<div className="role_left">
						<header className="left_header">
							<span className="role_left_title">角色列表</span>
							<Dropdown overlay={menu}>
								<Icon type="plus-square" className="icon_plus"/>
							</Dropdown>
						</header>
						<ul className="role_list">
							{data.map((item, index) => {
								return <li key={index}
								           className={item.selected ? "active pr" : "pr"}
								           onClick={this.handleClick.bind(this, item)}
								>{item.name}
									<Dropdown overlay={
										<Menu>
											<Menu.Item key="1"
											           onClick={this.handleMenuClick1.bind(this, "1", item)}>编辑</Menu.Item>
											<Menu.Item key="2"
											           onClick={this.handleMenuClick1.bind(this, "2", item)}>删除</Menu.Item>
										</Menu>
									}>
										<Icon
											style={{position: 'absolute', top: '5px', right: '5px'}}
											type="more"/>
									</Dropdown>
								</li>
							})}
						</ul>
					</div>
					<Tabs
						className="role_right"
						tabPosition="top" activeKey={activeKey} onChange={this.callback}>
						<TabPane
							tab="角色成员"
							key="1">
							<CastMembers
								name={name}
							/>
						</TabPane>
						<TabPane
							tab="权限设置"
							key="2">
							<Row style={{
								width: '100%',
								height: "54px",
								lineHeight: '54px',
								background: "rgba(250, 250, 250, 1)",
								border: "1px solid rgba(232, 232, 232, 1)",
								borderRadius: "4px",
								fontSize: "14px",
								fontWeight: "700px",
								color: "rgba(0, 0, 0, 0.847058823529412)"
							}}>
								<Col span={4} className="tc">
									模块
								</Col>
								<Col span={20} className="tc">
									权限分类
								</Col>
							</Row>
							<Row style={{
								width: '100%',
								height: "64px",
								lineHeight: '64px',
							}}>
								<Col span={4} className="tc">
									销售订单
								</Col>
								<Col span={20} className="tc">
									<Radio.Group onChange={this.onChangeRadio.bind(this, 'sales')}
									             value={this.state.sales}>
										<Radio value={"orders.sales.edit.all"}>编辑所有</Radio>
										<Radio value={"orders.sales.edit.self"}>编辑自己</Radio>
									</Radio.Group>
								</Col>
							</Row>
							<Row style={{
								width: '100%',
								height: "64px",
								lineHeight: '64px',
							}}>
								<Col span={4} className="tc">
									采购订单
								</Col>
								<Col span={20} className="tc">
									<Radio.Group onChange={this.onChangeRadio.bind(this, 'purchasing')}
									             value={this.state.purchasing}>
										<Radio value={"orders.purchasing.edit.all"}>编辑所有</Radio>
										<Radio value={"orders.purchasing.edit.self"}>编辑自己</Radio>
									</Radio.Group>
								</Col>
							</Row>
							<Row style={{
								width: '100%',
								height: "64px",
								lineHeight: '64px',
							}}>
								<Col span={4} className="tc">
									权限管理
								</Col>
								<Col span={20} className="tc">
									<Checkbox.Group
										options={
											[
												{label: '查看所有', value: 'orders.auth.view.all'},
												{label: '编辑所有', value: 'orders.auth.edit.all'}
											]
										}
										value={this.state.auth}
										onChange={this.onChangeCheck}
									/>
								</Col>
							</Row>
							<Row style={{
								width: '100%',
								height: "64px",
								lineHeight: '64px',
								marginTop: "20px"
							}}>
								<Col span={24} className="tc">
									<Button type="primary" onClick={this.handleSub}>保存</Button>
								</Col>
							</Row>
						</TabPane>
					</Tabs>
				</div>
				<Modal
					maskClosable={false}
					closable={false}
					okText={'提交'}
					title={this.state.title}
					visible={this.state.visible}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
				>
					<Form {...formItemLayout}>
						<Form.Item label="角色名称">
							{getFieldDecorator('name', {
								initialValue: this.state.name,
								rules: [{required: true, message: '请输入'}],
							})(<Input/>)}
						</Form.Item>
						{/*<Form.Item label="角色分组">
							{getFieldDecorator('applicationIds', {
								initialValue: "智见",
								rules: [{required: true, message: '请选择'}],
							})(<Input readOnly={true}/>)}
						</Form.Item>*/}
					</Form>
				</Modal>
			</div>
		)
	}
}


export default RoleNew;





