import React from 'react';
import {inject, observer} from 'mobx-react';
import {
	Button,
	Col,
	Row,
	Table,
	Pagination,
	ConfigProvider,
	Typography,
	message,
	Modal
} from 'antd';
import * as mobx from 'mobx';
import AddPerosn from './AddPerosn';
import _ from "lodash";

const {confirm} = Modal;
const {Title} = Typography;
const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);

@inject('UserStore')
@observer
class CastMembers extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			selectedRowKeys: [],
			add: {
				visible: false,
				data: [],
			}
		}
	}

	componentDidMount() {

	}

	onShowSizeChange = (current, pageSize) => {
		this.props.UserStore.rolepageIndex = current;
		this.props.UserStore.rolepageSize = pageSize;
		this.props.UserStore.getRoleList();
	}

	onChange = (pageNumber) => {
		this.props.UserStore.rolepageIndex = pageNumber;
		this.props.UserStore.getRoleList();
	}

	onSelectChange = selectedRowKeys => {
		this.setState({selectedRowKeys});
	};

	// 添加成员
	handAdd = () => {
		this.setState({
			add: Object.assign(this.state.add, {visible: true})
		}, () => {
			this.getSearch("");
		})
	}

	// 列表搜索
	getSearch = (param) => {
		const params = param;
		this.props.UserStore.getRoleData(params).then((res) => {
			this.setState({
				add: Object.assign(this.state.add, {data: res.body.map((item) => Object.assign(item, {selected: false}))})
			})
		});
	}

	updateState = (type, data) => {
		this.setState({
			[type]: Object.assign(this.state[type], data)
		})
	}

	getTableSearch = () => {
		this.props.UserStore.rolepageIndex = 1;
		this.props.UserStore.rolepageSize = 10;
		this.props.UserStore.getRoleList();
	}

	// 批量删除
	handleDel = () => {
		let _this = this;
		const {selectedRowKeys} = this.state;
		if (selectedRowKeys.length == 0) {
			message.warn("请先选择要删除的内容")
			return false;
		}
		confirm({
			title: '您确定要删除吗?',
			okText: '确定',
			okType: 'danger',
			cancelText: '取消',
			onOk() {
				const param = {
					userIds: selectedRowKeys,
					roleId: _this.props.UserStore.roleId
				}
				_this.props.UserStore.deleteUsersRole(param).then((res) => {
					if (res) {
						message.success('删除成功')
						_this.getTableSearch();
						_this.setState({
							selectedRowKeys: []
						})
					} else {
						message.error('删除失败');
					}
				});
			},
			onCancel() {
				console.log('Cancel');
			},
		});
	}

	render() {
		const {selectedRowKeys} = this.state;
		const columns = [
			{title: '序号', dataIndex: 'key', align: 'left'},
			{title: '姓名', dataIndex: 'realname', align: 'center'},
			{title: '岗位', dataIndex: 'station', align: 'center'},
			{title: '手机号码', dataIndex: 'phone', align: 'center'},
			{title: '邮箱账户', dataIndex: 'email', align: 'center'}
		];
		const {rolepageIndex, roletotalCount, roletableData} = this.props.UserStore;
		const rowSelection = {
			selectedRowKeys,
			onChange: this.onSelectChange,
		};

		return (
			<div>
				<Row type="flex" justify="space-between" className="">
					<Col span={6}>
						<Title
							style={{
								fontSize: '14px',
								fontFamily: 'PingFangSC-Medium,PingFang SC',
								fontWeight: '600',
								color: 'rgba(75,80,81,1)',
								padding: '15px 0 0 10px'
							}}
							level={3}>{this.props.name} ({this.props.name&&roletotalCount})</Title>
					</Col>
					<Col span={18} className="tr">
						<Button type="primary"
						        icon="delete"
						        size={"small"}
						        ghost
						        onClick={this.handleDel}
						>
							批量删除
						</Button>
						<Button type="primary"
						        icon="plus-circle"
						        size={"small"}
						        ghost
						        onClick={this.handAdd}
						        className="ml20">
							添加成员
						</Button>
					</Col>
				</Row>
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
					<Table
						className="mt20"
						columns={columns}
						dataSource={mobx.toJS(roletableData)}
						size="middle"
						pagination={false}
						rowKey={record => record.id}
						rowSelection={rowSelection}
					/>
					<Pagination
						className="mt20 tr"
						showSizeChanger
						onShowSizeChange={this.onShowSizeChange}
						onChange={this.onChange}
						total={roletotalCount}
						showQuickJumper
						current={rolepageIndex}
						showTotal={total => `共 ${total} 条记录`}
					/>
				</ConfigProvider>
				<AddPerosn
					{...this.state.add}
					updateState={this.updateState}
					getSearch={this.getSearch}
					getTableSearch={this.getTableSearch}
				/>
			</div>
		)
	}
}


export default CastMembers;





