import React from 'react';
import {inject, observer} from 'mobx-react';
import RoleNew from "../../components/report/RoleNew";

@inject('UserStore')
@observer
class UserRole extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
		}
	}

	componentDidMount() {
		// 获取roleID
		this.props.UserStore.getRoleByCodeAndAppId();
	}

	render() {
		return (
			<RoleNew/>
		)
	}
}


export default UserRole;




